<template>
  <section>
    <el-row class="row">
      <el-col :span="16" :offset="4">
        <el-carousel indicator-position="outside">
          <el-carousel-item v-for="item in 4" :key="item">
            <h3>{{ item }}</h3>
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>
    <el-row :gutter="20" type="flex" justify="center" class="row">
      <el-col v-for="(item,index) in cardContent" :key="index" :span="4">
        <el-card
          class="box-card"
          @mouseenter.native="cardActive=true"
          @mouseleave.native="cardActive=false"
        >
          <div class="bigText">{{item.name}}</div>
          <div v-show="!cardActive" class="contentText">{{item.content1}}</div>
          <div align="center">
            <el-button class="cardBtn" type="success" plain v-show="cardActive">{{item.content2}}</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <hr
      style="filter: alpha(opacity=100,finishopacity=0,style=3)"
      width="80%"
      color="#eef1f6"
      size="3"
    >
    <el-row type="flex" justify="center" class="row">
      <el-col :span="4">
        <div class="bigText">
          <i class="el-icon-view"></i>
          {{" 最新公告 :"}}
        </div>
      </el-col>
      <el-col :span="4" v-for="(news,index) in newsList" :key="index">
        <a class="contentText2" href="#">{{news}}</a>
      </el-col>
    </el-row>
    <hr
      style="filter: alpha(opacity=100,finishopacity=0,style=3)"
      width="80%"
      color="#eef1f6"
      size="3"
    >
    <el-row type="flex" justify="center" class="row">
      <el-col :span="24">
        <div class="bigText2">{{"OTCBTC 场外交易区"}}</div>
        <div
          class="contentText"
        >{{"支持多国法币购买 BTC 、 ETH 、 LTC 、 EOS 、 NEO 、 QTUM 、 ZEC 、 USDT 等多种主流数字币"}}</div>
        <div class="contentText">{{"下单 => 微信／支付宝转账=> 成交"}}</div>
        <el-row type="flex" justify="center" class="row">
          <el-col :span="4">
            <el-button type="success" class="cardBtn">{{"购买BTC"}}</el-button>
          </el-col>
          <el-col :span="4" plain>
            <el-button type="success" plain class="cardBtn">{{"出售BTC"}}</el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" :gutter="20">
      <el-col :span="5" v-for="o in 3" :key="o">
        <!-- {{tableData.length/3*(o-1)}}
        {{tableData.slice(tableData.length/3*(o-1),tableData.length/3*(o))}}-->
        <el-table
          :data="tableData.slice(tableData.length/3*(o-1),tableData.length/3*(o))"
          stripe
          :show-header="false"
        >
          <el-table-column prop="content" label="姓名" width="180"></el-table-column>
          <el-table-column prop="ctime" label="地址" align="right">
            <template slot-scope="scope">
              <el-button type="info" size="small">{{scope.row.ctime|getDateDetail}}</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" :gutter="20">
      <el-col :span="5">
        <el-card >
          <div slot="header" class="clearfix">
            <span class="bigText3">{{"新手入门"}}</span>
          </div>
        <ul>
              <li v-for="o in 4" :key="o" class="textItem">{{'列表内容 ' + o }}</li>
          </ul>
        </el-card>
      </el-col>
      <el-col :span="5">
        <el-card >
          <div slot="header" class="clearfix">
            <span class="bigText3">{{"常见问题"}}</span>
          </div>
        <ul>
              <li v-for="o in 4" :key="o" class="textItem">{{'列表内容 ' + o }}</li>
          </ul>
        </el-card>
      </el-col>
      <el-col :span="5">
        <el-card>
          <div slot="header" class="clearfix">
            <span class="bigText3">{{"验证问题"}}</span>
          </div>
          <ul>
              <li v-for="o in 4" :key="o" class="textItem">{{'列表内容 ' + o }}</li>
          </ul>
          
        </el-card>
      </el-col>
    </el-row>
  </section>
</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          content: "CNY => 0.322 ETH",
          ctime: new Date("Tue, 15 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "CNY => 18.571 EOS",
          ctime: new Date().toUTCString()
        },
        {
          content: "CNY => 6.198 EOS",
          ctime: new Date().toUTCString()
        },
        {
          content: "CNY => 0.322 ETH",
          ctime: new Date().toUTCString()
        },
        {
          content: "CNY => 18.571 EOS",
          ctime: new Date().toUTCString()
        },
        {
          content: "CNY => 6.198 EOS",
          ctime: new Date().toUTCString()
        },
        {
          content: "CNY => 0.322 ETH",
          ctime: new Date().toUTCString()
        },
        {
          content: "CNY => 18.571 EOS",
          ctime: new Date().toUTCString()
        },
        {
          content: "CNY => 6.198 EOS",
          ctime: new Date().toUTCString()
        }
      ],
      cardActive: false,
      newsList: [
        "服务器升级维护公告（1-10）",
        "支持 ETH 君士坦丁堡硬分叉升级（1-3)",
        " OTCBTC 场外区广告持续免费六个月（12-31）"
      ],
      cardContent: [
        {
          name: "场外交易区",
          content1: "使用法币购买/出售数字货币",
          content2: "立即购买"
        },
        {
          name: "场外交易区2",
          content1: "使用法币购买/出售数字货币2",
          content2: "立即购买3"
        },
        {
          name: "场外交易区3",
          content1: "使用法币购买/出售数字货币3",
          content2: "立即购买3"
        },
        {
          name: "场外交易区4",
          content1: "使用法币购买/出售数字货币3",
          content2: "立即购买4"
        }
      ]
    };
  },
  filters: {
    getDateDetail(date) {
      console.log(date);
      var d = new Date(date);
      var year = d.getFullYear();
      var month = d.getMonth() + 1;
      var day = d.getDate();
      var hour = d.getHours();
      var minute = d.getMinutes();
      var sec = d.getSeconds();

      var dNow = new Date();
      var yearNow = dNow.getFullYear();
      var monthNow = dNow.getMonth() + 1;
      var dayNow = dNow.getDate();
      var hourNow = dNow.getHours();
      var minuteNow = dNow.getMinutes();
      var secNow = dNow.getSeconds();
      if (year == yearNow) {
        if (month == monthNow) {
          if (day == dayNow) {
            if (hour == hourNow) {
              if (minute == minuteNow) {
                return secNow - sec + "秒前移交";
              } else {
                return minuteNow - minute + "分钟前移交";
              }
            } else {
              return hourNow - hour + "小时前移交";
            }
          } else {
            if (dayNow - day <= 3) {
              return dayNow - day + "天前移交";
            }
          }
        }
      }
      return year + "-" + month + "-" + day;
    }
  },
  methods: {},
  mounted() {
    var user = sessionStorage.getItem("user");
  }
};
</script>
<style>
.row,
hr {
  margin: 20px auto;
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
.bigText {
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  margin: 10px;
}
.bigText2 {
  font-size: 28px;
  font-weight: 700;
  text-align: center;
  margin: 10px;
}
.bigText3 {
  font-size: 18px;
  font-weight: 700;
  text-align: left;
  /* margin: 10px; */
}
.textItem {
  font-size: 14px;
  color: rgb(155, 155, 155);
  text-align: left;
  margin: 0 0 10px 0px;
}
.contentText {
  font-size: 14px;
  color: rgb(155, 155, 155);
  text-align: center;
  margin: 30px 0 10px 0;
}
.contentText2 {
  font-size: 14px;
  color: rgb(0, 172, 14);
  text-align: left;
  display: block;
  margin: 15px 0 10px 0;
}
.cardBtn {
  text-align: center;
  margin: 10px 0 10px 0;
  width: 80%;
}
.box-card {
  height: 150px;
}
</style>


